import React, { Component } from 'react';
import Typing from 'react-typing-animation';
import { connect } from 'react-redux';

import { BackgroundDiv } from './HeaderStyle.js';
import translations from '../Languages/translations';

class Header extends Component {

  constructor(props) {
    super(props);
    this.state = { textIndex: 0 };
    this.defineTypingText = this.defineTypingText.bind(this);
    this.updateTextIndex = this.updateTextIndex.bind(this);
  }

  updateTextIndex() {
    const { textIndex } = this.state;
    if (textIndex === 2) {
      this.setState(prevState => ({ ...prevState, textIndex: 0 }));
      return;
    }
    this.setState(prevState => ({ ...prevState, textIndex: textIndex + 1 }));
  }

  defineTypingText(index) {
    const language = localStorage.getItem('revtechweb_language');
    if (index === 0) {
      return translations[language].header['text1'];
    }
    if (index === 1) {
      return translations[language].header['text2'];
    }
    return translations[language].header['text3'];
  }

  render() {
    const { textIndex } = this.state;
    return (
      <BackgroundDiv>
        <div className="typed-text">
          <Typing hideCursor loop speed={40} onFinishedTyping={() => this.updateTextIndex()}>
            <span>{this.defineTypingText(textIndex)}</span>
            <Typing.Reset count={1} delay={2400} />
          </Typing>
        </div>
      </BackgroundDiv>
    );
  }
}

// typing animation crashes without connect
export default connect(null, null)(Header);
