import React from 'react';
import TweenMax from 'gsap/TweenMax';
import axios from 'axios';
import { connect } from 'react-redux';

import { ContactContainer } from './ContactStyle.js';
import translations from '../Languages/translations';

const API_URL = 'https://revtechweb-node-mailing.herokuapp.com/';

class Contact extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      form: {
        from: '',
        title: '',
        message: ''
      },
      isLoading: false,
      contactSent: false,
    };
    this.whatsappIcon = React.createRef();
    this.changeContactField = this.changeContactField.bind(this);
    this.submitContact = this.submitContact.bind(this);
    this.defineButtonContent = this.defineButtonContent.bind(this);
  }

  componentDidMount() {
    TweenMax.fromTo(
      this.whatsappIcon,
      1,
      { rotation: 0 },
      {
        rotation: 20,
        delay: 5,
        repeat: -1,
        repeatDelay: 0,
        yoyo: true,
        immediateRender: false
      }
    );
  }

  changeContactField(field, value) {
    const { form } = this.state;
    form[field] = value;
    this.setState(prevState => ({ ...prevState, form, contactSent: false }));
  }

  defineButtonContent() {
    const { isLoading, contactSent } = this.state;
    const { language } = this.props;
    if (isLoading) {
      return { label: '', icon: 'fa-spinner fa-spin'};
    }
    if (contactSent) {
      return { label: translations[language].contact.buttonLabel2, icon: 'fa-check' };
    }
    return { label: translations[language].contact.buttonLabel1, icon: 'fa-paper-plane' };
  }

  submitContact(event) {
    event.preventDefault();
    const { form } = this.state;
    const emptyForm = { from: '', title: '', message: '' };
    this.setState(prevState => ({ ...prevState, isLoading: true }));
    axios.post(API_URL, form).then(response => {
      this.setState(prevState => ({
        ...prevState,
        isLoading: false,
        contactSent: true,
        form: emptyForm
      }));
    }).catch(() => {
      this.setState(prevState => ({ ...prevState, isLoading: false }));
      alert('Erro ao enviar mensagem!')
    });
  }

  render() {
    const { language } = this.props;
    const { form, contactSent } = this.state;
    return (
      <ContactContainer>
        <div className="contact-title">
          <h1>{translations[language].contact.mainTitle}</h1>
        </div>
        <div className="contact-description-justified">
          {translations[language].contact.text1}
        </div>
        <br />
        <div
          className="whatsapp-badge"
          title={translations[language].contact.whatsappTitle}
          ref={(elem) => this.whatsappIcon = elem}
          onClick={() => window.open('http://api.whatsapp.com/send?1=pt_BR&phone=5519982079553', '_blank')}
        />
        <div className="form-container">
          <form onSubmit={this.submitContact}>
            <input
              className="form form-control"
              placeholder={translations[language].contact.placeholder1}
              value={form.from}
              onChange={(e) => this.changeContactField('from', e.target.value)}
              required
              type="email"
            />
            <br />
            <input
              className="form form-control"
              placeholder={translations[language].contact.placeholder2}
              value={form.title}
              onChange={(e) => this.changeContactField('title', e.target.value)}
              required
            />
            <br />
            <textarea
              className="form form-control"
              placeholder={translations[language].contact.placeholder3}
              rows="10"
              value={form.message}
              onChange={(e) => this.changeContactField('message', e.target.value)}
              required
            />
            <button className={`btn ${contactSent ? 'btn-success' : 'btn-primary'}`} type="submit">
              <span>{this.defineButtonContent().label}</span>
              <i className={`fa ${this.defineButtonContent().icon} fa-fw`} aria-hidden="true" />
            </button>
          </form>
        </div>
      </ContactContainer>
    );
  }
}

const mapStateToProps = state => ({ language: state.language.currentLanguage });
export default connect(mapStateToProps, null)(Contact);
