import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { TweenMax } from 'gsap/TweenMax';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { NavbarOptionStyle } from './NavbarOptionStyle';
import { changeLocation, toggleNavbar } from './services/navbarActions';

class NavbarOption extends Component {
  constructor(props) {
    super(props);
    this.animatedLine = React.createRef();
  }

  showLine() {
    TweenMax.to(this.animatedLine, 0.25, { css: { width: "50px" } });
  }

  hideLine() {
    TweenMax.to(this.animatedLine, 0.25, { css: { width: "0" } });
  }

  render() {
    const { name, changeLocation, toggleNavbar } = this.props;
    return (
      <NavbarOptionStyle
        onMouseEnter={() => this.showLine()}
        onMouseLeave={() => this.hideLine()}
        onClick={() => {
          toggleNavbar(false);
          changeLocation(name);
        }}
      >
        <span>{name}</span>
        <div
          ref={(elem) => { this.animatedLine = elem; }}
          className="animated-line"
        />
      </NavbarOptionStyle>
    );
  }
}

NavbarOption.propTypes = {
  name: PropTypes.string.isRequired,
};

const mapDispatchToProps = dispatch => bindActionCreators({
  changeLocation,
  toggleNavbar
}, dispatch);

export default connect(null, mapDispatchToProps)(NavbarOption);
