import React from 'react';
import { connect } from 'react-redux';

import { FooterContainer } from './FooterStyle';
import translations from '../Languages/translations';

const Footer = (props) => (
  <FooterContainer>
      <div className="logo" />
      <div className="footer-text">
        <span>{translations[props.language].footer.text1}</span>
      </div>
      <br />
      <div className="footer-text reserved-rights">
        <span>{translations[props.language].footer.text2}</span>
      </div>
  </FooterContainer>
);

const mapStateToProps = state => ({ language: state.language.currentLanguage });
export default connect(mapStateToProps, null)(Footer);
