const INITIAL_STATE = { location: { navbarLocation: 'Home', trigger: false }, navbarStatus: false };

export default function (state = INITIAL_STATE, action) {
  switch (action.type) {
    case 'CHANGE_LOCATION':
      return { ...state, location: { navbarLocation: action.payload, trigger: !state.location.trigger }};
    case 'TOGGLE_NAVBAR':
      return { ...state, navbarStatus: action.payload };
    default:
      return state;
  }
}
