import React from 'react';
import { connect } from 'react-redux';

import ClientsSlider from '../ClientsSlider/ClientsSlider';
import { AboutContainer } from './AboutStyle.js';
import translations from '../Languages/translations';

const About = (props) => (
  <AboutContainer>
    <div className="about-title">
      <h1>{translations[props.language].about.mainTitle}</h1>
    </div>
    <div className="about-content">
      <div className="about-description">
        <div className="logo-container" />
        <div className="text-container">
          <span>{translations[props.language].about.text1}</span>
        </div>
        <div className="text-container">
          <span>{translations[props.language].about.text2} <b>{translations[props.language].about.text3}</b></span>
        </div>
      </div>
      <div className="about-description">
        <div className="text-container clients-slider-text">
          <span>
            {translations[props.language].about.text4} <b>{translations[props.language].about.text5}</b> {translations[props.language].about.text6}
          </span>
        </div>
        <ClientsSlider />
      </div>
    </div>
  </AboutContainer>
);

const mapStateToProps = state => ({ language: state.language.currentLanguage });
export default connect(mapStateToProps, null)(About);
