import React, { Component } from 'react';
import { connect } from 'react-redux';

import icon1 from '../../static/icon1.png';
import icon2 from '../../static/icon2.png';
import icon3 from '../../static/icon3.png';
import ServicesCard from './ServicesCard';
import { ServicesCardsContainer, ServicesSection } from './ServicesStyle.js';
import translations from '../Languages/translations';

class Services extends Component {

  render() {
    const { language } = this.props;
    return (
      <ServicesSection>
        <div className="services-title">
          <h1>{translations[language].services.mainTitle}</h1>
        </div>
        <ServicesCardsContainer>
          <ServicesCard
            icon={icon1}
            title={translations[language].services.title1}
            description={translations[language].services.svc1}
          />
          <ServicesCard
            icon={icon2}
            title={translations[language].services.title2}
            description={translations[language].services.svc2}
          />
          <ServicesCard
            icon={icon3}
            title={translations[language].services.title3}
            description={translations[language].services.svc3}
          />
        </ServicesCardsContainer>
      </ServicesSection>
    );
  }
};

const mapStateToProps = state => ({ language: state.language.currentLanguage });
export default connect(mapStateToProps, null)(Services);
