import React, { Component } from 'react';
import TweenMax from 'gsap/TweenMax';
import ReactInterval from 'react-interval';

import aktienow from '../../static/aktienow.png'; 
import droz from '../../static/droz.png';
import hexagon from '../../static/hexagon.png';
import ibm from '../../static/ibm.png';
import vivo from '../../static/vivo.svg';
import hypnobox from '../../static/hypnobox.png';
import nleads from '../../static/99leads.png';
import { ClientsSliderContainer } from './ClientsSliderStyle';

const clients = [
  {
    name: 'aktienow',
    imageElement: aktienow,
    currentPosition: 'left',
  },
  {
    name: 'nleads',
    imageElement: nleads,
    currentPosition: 'left',
  },
  {
    name: 'droz',
    imageElement: droz,
    currentPosition: 'left',
  },
  {
    name: 'hexagon',
    imageElement: hexagon,
    currentPosition: 'center',
  },
  {
    name: 'ibm',
    imageElement: ibm,
    currentPosition: 'right',
  },
  {
    name: 'vivo',
    imageElement: vivo,
    currentPosition: 'right',
  },
  {
    name: 'hypnobox',
    imageElement: hypnobox,
    currentPosition: 'right',
  }
]

class ClientsSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isChangingClient: false,
    };
    clients.forEach(client => {
      this[client.name] = React.createRef();
    })
  }

  leftStack = []
  
  currentClient = null

  rightStack = []

  componentDidMount() {
    clients.forEach((client, index) => {
      if (index < Math.floor(clients.length / 2)) {
        this.leftStack.push(client);
        return;
      }
      if (index === Math.floor(clients.length / 2)) {
        this.currentClient = client;
        return;
      }
      this.rightStack.push(client);
    });
  }

  changeClientLogo(direction) {
    const { isChangingClient } = this.state;
    if (isChangingClient) {
      return;
    }
    this.setState({ isChangingClient: true });
    setTimeout(() => {
      this.setState({ isChangingClient: false });
    }, 1000)
    const changeToLeft = () => {
      TweenMax.to(this[this.currentClient.name], 0.7, { css: {left: '0px'} });
      setTimeout(() => {
        TweenMax.to(this[this.rightStack[0].name], 0.7, { css: {left: '167px'} });
        this.leftStack.push(this.currentClient);
        this.currentClient = this.rightStack[0];
        this.rightStack.shift();
      }, 300);
    }

    const changeToRight = () => {
      const index = this.leftStack.length - 1;
      TweenMax.to(this[this.currentClient.name], 0.7, { css: {left: '334px'} });
      setTimeout(() => {
        TweenMax.to(this[this.leftStack[index].name], 0.7, { css: {left: '167px'} });
        this.rightStack.unshift(this.currentClient);
        this.currentClient = this.leftStack[index];
        this.leftStack.pop();
      }, 300);
    }

    if (direction === 'left' && this.rightStack.length) {
      changeToLeft();
      return;
    }
    if (direction === 'right' && this.leftStack.length) {
      changeToRight();
      return;
    }
    if (direction === 'left' && !this.rightStack.length) {
      TweenMax.to(this[this.leftStack[0].name], 0, { css: {left: '334px'} });
      this.rightStack.push(this.leftStack[0]);
      this.leftStack.shift();
      changeToLeft();
      return;
    }
    if (direction === 'right' && !this.leftStack.length) {
      const index = this.rightStack.length - 1;
      TweenMax.to(this[this.rightStack[index].name], 0, { css: {left: '0px'} });
      this.leftStack.push(this.rightStack[index]);
      this.rightStack.pop();
      changeToRight();
      return;
    }
  }

  render() {
    return (
      <ClientsSliderContainer>
        <ReactInterval
          timeout={5000}
          enabled={true}
          callback={() => this.changeClientLogo('right')}
        />
        <div className="slider-control slider-left">
          <div className="control-container" onClick={() => this.changeClientLogo('left')}>
            <span className="glyphicon glyphicon-chevron-left" />
          </div>
        </div>
        {
          clients.map(client => (
            <div
              className={`${client.currentPosition}-client-logo`}
              ref={(elem) => { this[client.name] = elem; }}
              key={`logo_${client.name}`}
              style={{
                background: `url(${client.imageElement}) no-repeat`,
                backgroundSize: 'contain',
                backgroundPosition: 'center',
              }}
            />
          ))
        }
        <div className="slider-control slider-right">
          <div className="control-container" onClick={() => this.changeClientLogo('right')}>
            <span className="glyphicon glyphicon-chevron-right" />
          </div>
        </div>
      </ClientsSliderContainer>
    );
  }
}

export default ClientsSlider;
