import React, { Component } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import { NavbarContainer, SmallDevicesNavbar, SmallNavbarContainer } from './NavBarStyle';
import { toggleNavbar } from './services/navbarActions';
import NavbarOption from './NavbarOption';
import Languages from '../Languages/Languages';
import translations from '../Languages/translations';

class Navbar extends Component {
  constructor(props) {
    super(props);
    this.sideNavbar = React.createRef();
    this.switchNavbar = this.switchNavbar.bind(this);
  }

  switchNavbar(status) {
    const { toggleNavbar } = this.props;
    toggleNavbar(status);
  }

  render() {
    const { navbarStatus, language } = this.props;
    return (
      <div>
        <SmallDevicesNavbar>
          <div className="burguer-menu" onClick={() => this.switchNavbar(true)}>
            <span className="glyphicon glyphicon-menu-hamburger" />
          </div>
          <div className="logo-vertical" />
          <Languages />
        </SmallDevicesNavbar>
        {navbarStatus && <div>
          <SmallNavbarContainer>
            <div className="options-container">
              <NavbarOption name={translations[language].navbar.home} />
              <NavbarOption name={translations[language].navbar.services} />
              <NavbarOption name={translations[language].navbar.about} />
              <NavbarOption name={translations[language].navbar.contact} />
              <div
                className="close-button"
                onClick={() => this.switchNavbar(false)}
              >
                <span className="glyphicon glyphicon-remove" aria-hidden="true"></span>
              </div>
            </div>
          </SmallNavbarContainer>
        </div>
        }
        <NavbarContainer>
          <div className="logo-horizontal" />
          <div className="options-container">
            <NavbarOption name={translations[language].navbar.home} />
            <NavbarOption name={translations[language].navbar.services} />
            <NavbarOption name={translations[language].navbar.about} />
            <NavbarOption name={translations[language].navbar.contact} />
            <Languages />
          </div>
        </NavbarContainer>
      </div>
    );
  }
};

const mapStateToProps = state => ({
  navbarStatus: state.navbar.navbarStatus,
  language: state.language.currentLanguage
});

const mapDispatchToProps = dispatch => bindActionCreators({
  toggleNavbar
}, dispatch);

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);
