import React, { Component } from 'react';
import { Element, scroller } from 'react-scroll';
import { connect } from 'react-redux';

import Header from './Components/Header/Header';
import Services from './Components/Services/Services';
import About from './Components/About/About';
import Contact from './Components/Contact/Contact';
import './App.css';
import Navbar from './Components/Navbar/Navbar';
import Footer from './Components/Footer/Footer';

class App extends Component {

  constructor(props) {
    super(props);
    this.scrollTo = this.scrollTo.bind(this);
  }

  componentDidUpdate(prevProps) {
    const { location } = this.props;
    if (prevProps.location.trigger !== location.trigger) {
      this.scrollTo(location.navbarLocation);
    }
  }

  scrollTo(location) {
    setTimeout(() => {
      if (location === 'Serviços' || location === 'Services') {
        scroller.scrollTo('Services', {
          duration: 1000,
          delay: 100,
          offset: -80,
          smooth: true,
        });
        return;
      }
      if (location === 'Sobre' || location === 'About') {
        scroller.scrollTo('About', {
          duration: 1000,
          delay: 100,
          offset: -80,
          smooth: true,
        });
        return;
      }
      if (location === 'Contato' || location === 'Contact') {
        scroller.scrollTo('Contact', {
          duration: 1000,
          delay: 100,
          offset: -80,
          smooth: true,
        });
        return;
      }
      scroller.scrollTo('Header', {
        duration: 1000,
        delay: 100,
        smooth: true,
      });
    }, 300);
    
  }

  render() {
    const { navbarStatus } = this.props.location;
    return (
      <div style={navbarStatus ? { position: 'fixed', overflow: 'hidden', width: '100%' } : {}}>
        <Navbar />
        <Element name="Header">
          <Header />
        </Element>
        <Element name="Services">
          <Services />
        </Element>
        <Element name="About">
          <About />
        </Element>
        <Element name="Contact">
          <Contact />
        </Element>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  location: state.navbar.location,
  navbarStatus: state.navbar.navbarStatus
});

export default connect(mapStateToProps, null)(App);
